@import "../base/variables";
@import "../base/mixins";

// boutton
/* ‚úÖ Media Queries pour Tablette et Desktop */
@media screen and (min-width: 768px) {
    .btn {
        margin-top: 18px;
        margin-bottom: 27px;
        /* R√©duction de la marge pour les tablettes */
    }
}

@media screen and (min-width: 1440px) {
    .btn {
        margin-top: 18px;
        margin-bottom: 27px;
        /* R√©duction de la marge pour les desktops */
    }
}


// setps
@media screen and (min-width: 768px) {
    .steps {
        max-width: 900px; // ‚úÖ D√©finit une largeur max pour √©viter un affichage trop large
        margin: 0 auto; // ‚úÖ Centre le bloc horizontalement
    }

    .steps__container {
        display: flex; // ‚úÖ Passe en Flexbox
        flex-direction: row; // ‚úÖ Aligne les steps en ligne
        justify-content: space-between; // ‚úÖ Espacement uniforme
        align-items: center;
        max-width: 1053px;
        margin: 0 auto; // ‚úÖ Centre le contenu
    }

    .steps__list {
        display: flex;
        flex-direction: row; // ‚úÖ Change l'affichage des steps en ligne
        justify-content: space-between;
        flex-wrap: nowrap; // ‚úÖ Emp√™che le retour √† la ligne
        gap: 24px;
        width: 100%;
    }

    .step {
        max-width: 335px; // ‚úÖ Limite la largeur des steps
        flex: 1; // ‚úÖ Permet un bon √©quilibre entre les steps
    }
}

/* ‚úÖ Version Desktop (1024px et plus) */
@media screen and (min-width: 1024px) {
    .steps {
        max-width: 1053px; // ‚úÖ Ajuste la largeur pour desktop
        margin: 0 auto; // ‚úÖ Centre le bloc horizontalement
    }

    .steps__container {
        max-width: 1200px; // ‚úÖ Ajuste la largeur pour desktop
        grid-template-columns: 1fr 3fr; // ‚úÖ Aligne correctement les steps et le titre
        justify-content: space-between;
        align-items: center;
    }
}

@media screen and (min-width: 1440px) {
    .steps__container {
        max-width: 1200px; // ‚úÖ Ajuste la largeur pour grands √©crans
    }
}